import React, { useState } from "react";

import QrCodeIcon from "@mui/icons-material/QrCode";
import ShareIcon from "@mui/icons-material/Share";
import Box from "@mui/material/Box";
import Chip from "@mui/material/Chip";
import DialogContent from "@mui/material/DialogContent";
import Grid from "@mui/material/Grid";
import IconButton from "@mui/material/IconButton";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";

import QRCodeImg from "../../../../assets/images/QRCode.jpg";
import CMDialog from "../../../ui-components/dialog/CMDialog";
import { useSelector } from "react-redux";

const QRCodeModal = (props) => {
  const [QRModalOpen, setQRModalOpen] = useState(false);

  const handleClickOpen = () => {
    setQRModalOpen(true);
  };
  const handleClose = () => {
    setQRModalOpen(false);
  };
  const consultantData = useSelector((state) => state.common.consultantProfile.consultantData);
  return (
    <>
      <IconButton
        color="secondary"
        size="large"
        onClick={() => handleClickOpen()}
      >
        <QrCodeIcon fontSize="inherit" />
      </IconButton>
      <CMDialog
        dialogOpen={QRModalOpen}
        maxWidth="xs"
        dialogClose={handleClose}
      >
        <DialogContent dividers>
          <Grid container>
            <Grid item xs={12} className="text-center">
              <Box
                component="img"
                src={QRCodeImg}
                alt="codeImg"
                sx={{ maxWidth: "250px", mx: "auto" }}
              />

              <Stack
                direction="row"
                alignItems="center"
                justifyContent="center"
                className="flex-wrap"
              >
                <Chip
                  label={consultantData.firstName}
                  variant="outlined"
                  color="primary"
                  size="large"
                  className="my-2"
                />
                <IconButton color="secondary" size="small">
                  <ShareIcon fontSize="inherit" />
                </IconButton>
              </Stack>
              <Typography variant="body2">
                NoShare your profile link instantly with a QR code.
              </Typography>
            </Grid>
          </Grid>
        </DialogContent>
      </CMDialog>
    </>
  );
};

export default QRCodeModal;
