import React from "react";

import Box from "@mui/material/Box";
import Logo from "./Logo";

const LogoFooter = ({ align, sx }) => {
  return (
    <>
      <Box className="py-1">
        <Logo fullLogo={true} sx={{ width: "100px", ...sx }} />
      </Box>
    </>
  );
};

export default LogoFooter;
