import React, { useEffect, useState } from "react";

import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Container from "@mui/material/Container";
import Divider from "@mui/material/Divider";
import Grid from "@mui/material/Grid";
import IconButton from "@mui/material/IconButton";

import { get, isEmpty } from "lodash";
import { useDispatch, useSelector } from "react-redux";
import { useLocation, useNavigate, useParams } from "react-router-dom";

import Iconify from "../../components/common/Iconify";
import LogoFooter from "../../components/common/LogoFooter";
import { expireTime, setCustomCookie } from "../../components/hooks/cookie";
import useResponsive from "../../components/hooks/useResponsive";
import ProfileDetails from "../../components/pages/profile/nutritionist-profile/ProfileDetails";
import ProfileSkelton from "../../components/pages/profile/nutritionist-profile/ProfileSkelton";
import ProfileTopHead from "../../components/pages/profile/nutritionist-profile/ProfileTopHead";
import BookingModal from "../../components/pages/profile/single-profile/BookingModal";
import ProfileHeader from "../../components/pages/profile/single-profile/ProfileHeader";
import RecentArticles from "../../components/pages/profile/single-profile/RecentArticles";
import RecentRecipes from "../../components/pages/profile/single-profile/RecentRecipes";
import EmptyTableRowOverlay from "../../components/ui-components/data-grid/EmptyTableRowOverlay";
import { getConsultantProfile } from "../../store/slice/common";

const SingleProfile = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const params = useParams();
  const isMobileView = useResponsive("down", "sm");
  const { pathname } = useLocation();

  const consultantId = params.consultantId;
  const consultantData = useSelector(
    (state) => state.common.consultantProfile.consultantData
  );
  const articleBlogData = useSelector(
    (state) => state.common.consultantProfile.articleBlog
  );
  const recipeData = useSelector(
    (state) => state.common.consultantProfile.recipe
  );
  const consultantInfoLoading = useSelector(
    (state) => state.common.consultantInfoLoading
  );
  const [modalOpen, setModalOpen] = useState(isMobileView ? false : true);

  useEffect(() => {
    dispatch(getConsultantProfile(consultantId))
      .unwrap()
      .then((result) => {
        if (result.status) {
          if (isEmpty(get(result, "data", []))) {
            navigate("/");
          } else {
            setCustomCookie("consultantProfile", pathname, "/", expireTime);
          }
        } else {
          navigate("/");
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }, [consultantId, dispatch, navigate, pathname]);

  return (
    <>
      {consultantInfoLoading ? (
        <Container maxWidth={false} className="py-3">
          <ProfileSkelton />
        </Container>
      ) : isEmpty(consultantData) ? (
        <EmptyTableRowOverlay />
      ) : (
        <>
          <ProfileHeader />
          <Box component="section" className="w-100" pt={8.5}>
            <Container maxWidth={false}>
              <Grid
                container
                spacing={2}
                justifyContent={{ xs: "center", lg: "space-between" }}
              >
                <Grid item xs={12} md={10} lg={8} xl={8}>
                  <ProfileTopHead consultantDetail={consultantData} />
                  <ProfileDetails consultantDetail={consultantData} />
                </Grid>
                <Grid
                  item
                  xs={12}
                  md={10}
                  lg={4}
                  xl={3}
                  container
                  spacing={1}
                  className="h-fit-content"
                >
                  <Grid item xs={12} sm={8} md={6} lg={12}>
                    <RecentArticles recentArticles={articleBlogData} />
                    <Box className="text-right">
                      <Button
                        variant="outlined"
                        onClick={() => navigate("/article")}
                      >
                        Load More
                      </Button>
                    </Box>
                  </Grid>
                  <Grid item xs={12}>
                    <Divider light />
                  </Grid>
                  <Grid item xs={12} md={6} lg={12}>
                    <RecentRecipes recentArticles={recipeData} />
                    <Box className="text-right">
                      <Button
                        variant="outlined"
                        onClick={() => navigate("/recipe")}
                      >
                        Load More
                      </Button>
                    </Box>
                  </Grid>
                  {isMobileView ? (
                    <Grid item xs={12}>
                      <Button variant="contained" fullWidth>
                        Save Contact
                      </Button>
                    </Grid>
                  ) : null}
                </Grid>
              </Grid>
              <LogoFooter />
            </Container>
          </Box>
        </>
      )}
      <IconButton
        aria-label="booking"
        color="primary"
        className="position-fixed"
        sx={{
          bottom: "20px",
          right: "15px",
          backgroundColor: "primary.contrastText",
          boxShadow: (theme) => theme.shadows[15],
        }}
        onClick={() => setModalOpen(true)}
      >
        <Iconify
          icon="icon-park-twotone:appointment"
          sx={{ width: "30px", height: "30px" }}
        />
      </IconButton>
      {modalOpen ? <BookingModal setModalOpen={setModalOpen} /> : null}
    </>
  );
};

export default SingleProfile;
