import React, { useState } from "react";

import Button from "@mui/material/Button";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import { alpha, useTheme } from "@mui/material/styles";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import Stack from "@mui/material/Stack";
import IconButton from "@mui/material/IconButton";

import { motion } from "framer-motion";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";

import bookingCharacter from "../../../../assets/images/character_11.png";
import { MotionContainer, varBounceIn } from "../../../common/animate";
import BookingBasicInfo from "./BookingBasicInfo";
import Iconify from "../../../common/Iconify";

const BookingModal = (props) => {
  const { setModalOpen } = props;
  const theme = useTheme();

  const navigate = useNavigate();
  const varLow = alpha(theme.palette.primary.main, 0.9);
  const varHigh = alpha(theme.palette.primary.dark, 1);

  const [open, setOpen] = useState(false);

  const handleClose = () => {
    setOpen(false);
  };

  const isLoggedIn = useSelector((state) => state.auth.isLoggedIn);

  const handleLogin = () => {
    navigate("/login");
  };
  return (
    <>
      <MotionContainer
        initial="initial"
        open
        className="position-fixed"
        sx={{ bottom: "20px", right: "15px" }}
      >
        <motion.div variants={varBounceIn}>
          <Card
            sx={{
              backgroundImage: `linear-gradient(135deg, ${varLow} 0%, ${varHigh} 100%)`,
              mt: 7.5,
              maxWidth: { sm: "450px", xs: "300px" },
            }}
            className="position-relative overflow-visible"
          >
            <Stack direction="row" alignItems="start" justifyContent="end">
              <Box
                component="img"
                src={bookingCharacter}
                className="position-absolute"
                sx={{ top: "-60px", left: "20px", maxWidth: "150px" }}
              />
              <IconButton
                className="text-white p-3"
                onClick={() => setModalOpen(false)}
              >
                <Iconify icon="eva:close-fill" />
              </IconButton>
            </Stack>

            <CardContent sx={{ pt: 14 }}>
              <Typography variant="h4" className="text-white mb-2">
                Book Your Appointment First
              </Typography>
              <Typography variant="body2" className="text-white mb-4">
                Lorem ipsum, dolor sit amet consectetur adipisicing elit.
                Corporis, libero.Lorem ipsum, dolor sit amet consectetur
                adipisicing elit. Corporis, libero.
              </Typography>
              {isLoggedIn ? (
                <Button
                  variant="contained"
                  color="warning"
                  fullWidth
                  onClick={() => setOpen(true)}
                >
                  Booking Now
                </Button>
              ) : (
                <Button
                  variant="contained"
                  color="warning"
                  fullWidth
                  onClick={() => handleLogin()}
                >
                  Login
                </Button>
              )}
            </CardContent>
          </Card>
        </motion.div>
      </MotionContainer>
      {isLoggedIn && open ? (
        <BookingBasicInfo handleClose={handleClose} open={open} />
      ) : null}
    </>
  );
};

export default BookingModal;
